* {
  margin: 0;
  padding: 0;
}

body {
  height: 100vh;
}



.header {
  height: 100px;
  text-align: center;
  background: #62dade;
  background: -webkit-linear-gradient(
    to right,
    #8cbeff,
    #62a2de
  ); 
  background: linear-gradient(
    to right,
    #8cb0ff,
    #626ade
  ); 

  font-family: "Times New Roman", sans-serif;
  display: grid;
  align-items: center;
  color: white;
  text-transform: uppercase;
}

.header h1 {
  font-weight: 300;
  font-size: 60px;
  letter-spacing: 0.1rem;
}



.main {
  display: grid;
  grid-template-columns: 3fr 2fr;
  grid-column-gap: 50px;
  margin-top: 50px;
}



.typing {
  display: grid;
  justify-items: right;
}

.input-section {
  width: 90%;
  
  border: 1px solid black;
  font-family: "Verdana", monospace;
  display: grid;
  grid-template-rows: 437px 60px;
}

.text-area {
  padding: 8px;
}

.text-area p {
  font-size: 20px;
  line-height: 10px;
}

.text-area p .word {
  display: inline-block;
}

.active {
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 7px;
}

.correct {
  color: blue;
}

.incorrect {
  color: rgb(238, 51, 51);
}

.letter-active {
  color: white;
  background-color: rgb(126, 123, 123);
}


.letter-valid {
  color: rgba(0, 0, 0, 0.4);
}

.letter-invalid {
  color: white;
  background-color: red;
}

.space {
  width: 17px;
  height: 30px;
  position: relative;
  top: 8px;
  border-radius: 4px;
  display: inline-block;
  margin: 0 3px;
}

.input-section input {
  font-family: "Times New Roman", monospace;
  text-align: center;
}

.input-section input:focus {
  outline: none;
}

.text-input {
  width: 100%;
  box-sizing: border-box;
  height: 50px;
  border: none;
  border-top: 1px solid rgb(0, 0, 0);
  font-size: 28px;
  padding: 10px;
}

.text-input::placeholder {
  opacity: 0.5;
}

.details {
  font-size: 20px;
}

.details-content {
  display: grid;
  border: 1px solid;
  padding: 10px;
  height: 470px;
  justify-items: center;
  width: 200px;
}

.time-section p {
  display: inline-block;
  background: #3c4d5c;
  color: white;
  padding: 10px;
  letter-spacing: 1px;
}

.speed-section {
  margin-top: 30px;
}

.speed-heading-section {
  display: grid;
  grid-template-columns: 25px 1fr;
}

.speed-section img {
  width: 22px;
  opacity: 0.5;
}

.speed-heading {
  display: inline-block;
  margin-left: 10px;
  opacity: 0.5;
  font-size: 18px;
}

.speed-value {
  color: blue;
  font-size: 45px;
  text-align: center;
}

.speed-value span {
  font-size: 12px;
}

.word-count-section {
  margin-top: 30px;
  width: 100%;
}

.word-count-section p {
  background: #d4f0f5;
  margin-bottom: 10px;
  display: grid;
  grid-template-columns: 3fr 0.6fr;
}

.reset-button {
  background-color: #38b44a;
  color: white;
  font-size: 18px;
  padding: 2px 8px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.reset-button:hover {
  background-color: #2c8d3a;
  border-color: #298537;
}

.reset-button:focus {
  outline: none;
}